import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import PriceFixed from "../PriceFixed";

const useStyles = makeStyles((theme) => ({
    rootBg: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type PriceFixedStoryProps = {
    mode: string,
    brand: BrandType,
    dailyPrice: string,
    totalDays: string
    totalPrice: string
    suffix: string
    prefix: string
    labelButton: string
}

const PriceFixedStory = ({
    mode, brand, dailyPrice, totalDays, totalPrice, suffix, prefix, labelButton
}: PriceFixedStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="PriceFixed">
                <Typography variant="h3">PriceFixed</Typography>
                <br />
                <Divider />
                <br />
                <Grid item xs={6}>
                    <PriceFixed
                        dailyPrice={dailyPrice}
                        totalDays={totalDays}
                        totalPrice={totalPrice}
                        suffix={suffix}
                        prefix={prefix}
                        className=""
                        labelButton={labelButton}
                    />
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "rootBg",
        dailyPrice: "2000,00",
        totalDays: "3",
        totalPrice: "6000,00",
        suffix: "dias",
        prefix: "dia",
        labelButton: "Default Button",
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["rootBg", "darkBg"]
        },
        dailyPrice: { type: "string" },
        totalDays: { type: "string" },
        totalPrice: { type: "string" },
        suffix: { type: "string" },
        prefix: { type: "string" },
        labelButton: { type: "string" }
    }
}

storiesOf("Components/PriceFixed", module)
    .add("Default", (args) => <PriceFixedStory {...args}/>, argsControls);
